import React, { Component } from 'react';
import MenuNav from '../pages/MenuNav';
import { Form,Button,Segment,Label,Grid } from 'semantic-ui-react';
import {crearUser} from "../../actions/usersAction";
import {Link} from "react-router-dom";
import { connect } from 'react-redux';


class Agregar extends Component {

    state ={
        nombre: "",
        municipio: "",
        direccion: "",
        cedula: "",
        password:""
    };
    handleSave = (e) => {
        e.preventDefault();
        this.props.crearUser({
            nombre: this.state.nombre,
            municipio: this.state.municipio,
            direccion: this.state.direccion,
            cedula: this.state.cedula,
            password:this.state.password
        });
        
        this.props.history.push("/Users");
    }
    changeInput = (event) => {
        this.setState({[event.target.name]:event.target.value});
    }
    render() {
        return (
            <div>
            <MenuNav/>
            <Grid style={{ marginTop: '7em' }} columns={1}>
            <Grid.Column>
            <Segment raised>
                <Label color='teal' ribbon>
                Registro de Caficultor
                </Label>
                <Form style={{ marginTop: '1em' }}>
                    <Form.Field>
                    <label>Nombre</label>
                    <input  name="nombre" placeholder='Nombre' onChange={this.changeInput} />
                    </Form.Field>
                    <Form.Field>
                    <label>Municipio</label>
                    <input  name="municipio" placeholder='Municipio' onChange={this.changeInput} />
                    </Form.Field>
                    <Form.Field>
                    <label>Direccion</label>
                    <input  name="direccion" placeholder='Direccion' onChange={this.changeInput} />
                    </Form.Field>
                    <Form.Field>
                    <label>Cedula</label>
                    <input 
                        name="cedula"
                        placeholder='Cedula'
                        type='number'
                        onChange={this.changeInput} />
                    </Form.Field>
                    <Form.Field>
                    <label>Password</label>
                    <input 
                        name="password"
                        placeholder='Password'
                        type='password'
                        onChange={this.changeInput} />
                    </Form.Field>
                    <Button onClick={this.handleSave} primary type='submit'>Agregar</Button>
                    <Link to="/Users"><Button type='submit'>Regresar</Button></Link>
                </Form>
            </Segment>
            </Grid.Column>
            </Grid>
            </div>
        );
    }
}

export default connect(null,{crearUser})(Agregar);