import React, { Component } from 'react';
import {Grid,Label,Segment,List, Button} from "semantic-ui-react";
import {Link} from "react-router-dom";
import MenuNav from '../pages/MenuNav';
import {connect} from "react-redux";


class Ver extends Component {
    state ={};
    componentDidMount(){
     const users = this.props.users.filter(p=>p.cedula == this.props.cc );    
     this.setState(users[0]);
     console.log(this.props.users);
     console.log(this.props.cc);
     console.log(users);

    }
    render() {
        return (
        <div>
        <MenuNav/>
        <Grid style={{ marginTop: '7em' }} columns={1}>
            <Grid.Column>
            <Segment raised>
                <Label color='green' ribbon>
                Caficultor
                </Label>
                <span>Detalles de la Cuenta</span>
                <List>
                    <List.Item>
                    <List.Content>
                        <List.Header>Nombre</List.Header>
                        <List.Description>{this.state.nombre}</List.Description>
                    </List.Content>
                    </List.Item>
                    <List.Item>    
                    <List.Content>
                        <List.Header>Municipio</List.Header>
                        <List.Description>{this.state.municipio}</List.Description>
                    </List.Content>
                    </List.Item>
                    <List.Item>    
                    <List.Content>
                        <List.Header>Dirección</List.Header>
                        <List.Description>{this.state.direccion}</List.Description>
                    </List.Content>
                    </List.Item>
                    <List.Item>    
                    <List.Content>
                        <List.Header>Cedula</List.Header>
                        <List.Description>{this.state.cedula}</List.Description>
                    </List.Content>
                    </List.Item>
                    <List.Item style={{ marginTop: '1em' }}>    
                    <List.Content>
                        <List.Header></List.Header>
                        <List.Description>
                            <Link to={`/VerCultivos/${this.state._id}`}><Button primary >Ver Cultivos</Button></Link>
                            <Link to="/Users"><Button >Regresar</Button></Link>
                        </List.Description>
                    </List.Content>
                    </List.Item>
                </List>
            </Segment>
            </Grid.Column>
        </Grid>    
        </div>
        );
    }
}
const mapStateToProp =(state)=>{
    return{
        users: state.users.users
    };
  }
  export default connect(mapStateToProp,null)(Ver);
  
  